import React from "react";
import Helmet from "react-helmet";
import Layout from "components/Layout";
import Container from "components/Container";

import img_gatsby from "assets/images/cover.jpg";

const IndexPage = () => {
  return (
    <Layout pageName="home">
      <Helmet>
        <title>Home Page</title>
      </Helmet>
      <Container>
        <p>Filmmaker/Videographer</p>
	<p className="cover">
          <iframe width="100%" height="500" src="https://www.youtube.com/embed/mXRofIF8VWA" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        </p>
<p>
Hi Everyone! Welcome to my website. I am a filmmaker based in South Africa. If you want you can visit my YouTube channel by clicking on the link below, or watch one of my videos as displayed above. <br /> <br />

Want to contact me for job hire or other opportunities more business-related? Then you can do so by clicking my email link or by contacting me through any of my social links also listed below.</p>
      </Container>
    </Layout>
  );
};

export default IndexPage;
